export default function arrayConversion(oldArray, newArray) {
  const oldPixelArray = oldArray;
  const newPixeArray = newArray;
  //   conversion     .
  //    0,   .
  //   ,       .
  //   0,   .
  //   ,    o .
  const oldArraySize = oldPixelArray[oldPixelArray.length - 1][5];
  const newArraySize = newPixeArray[newPixeArray.length - 1][5];
  const conversion = newArraySize - oldArraySize;
  if (conversion >= 0) {
    for (let i = 0; i < oldPixelArray.length; i += 1) {
      // eslint-disable-next-line prefer-destructuring
      newPixeArray[
        i + ((newArraySize + 1)
                * conversion / 2) + conversion / 2 + conversion
                * oldPixelArray[i][5]][4] = oldPixelArray[i][4];
    }
  } else {
    for (let i = 0; i < newPixeArray.length; i += 1) {
      // eslint-disable-next-line prefer-destructuring
      newPixeArray[i][4] = oldPixelArray[
        i + ((oldArraySize + 1)
                * (-1) * conversion / 2) + (-1) * conversion / 2 + (-1)
                * conversion * newPixeArray[i][5]][4];
    }
  }
  return newPixeArray;
}
